<template>
  <div class="login-regist">
    <div v-if="isLogin" class="login">
      <div class="login-title">登录</div>
      <input type="text" name="username" class="user-name" placeholder="请输入用户名" v-model="userName" @change="userChange">
      <div class="res uname">
        <div v-show="isUserName">请输入3位以上的用户名</div>
      </div>
      <input type="password" name="userpsw" class="user-psw" placeholder="请输入密码" v-model="userPsw" @change="pswChange">
      <div class="res upsw">
        <div v-show="isUserPsw">请输入6位以上的密码</div>
      </div>
      <div class="login-button" @click="userLogin">登录</div>
    </div>
    <div v-else class="regist">
      <div class="regist-title">注册</div>
      <input type="text" name="regname" class="reg-name" placeholder="请输入用户名" v-model="regName" @change="regNameChange">
      <div class="res rname">
        <div v-show="isRegName">请输入3位以上的用户名</div>
      </div>
      <input type="password" name="regpsw1" class="reg-psw1" placeholder="请输入密码" v-model="regPsw1" @change="regPsw1Change">
      <div class="res rpsw1">
        <div v-show="isRegPsw1">请输入6位以上的密码</div>
      </div>
      <input type="password" name="regpsw2" class="reg-psw2" placeholder="请输入密码" v-model="regPsw2" @change="regPsw2Change">
      <div class="res rpsw2">
        <div v-show="isRegPsw2">两次输入的密码不一致</div>
      </div>
      <input type="text" name="regemail" class="reg-email" placeholder="请输入邮箱" v-model="regEmail" @change="regEmailChange">
      <div class="res remail">
        <div v-show="isRegEmail">请输入正确的邮箱</div>
      </div>
      <div class="regist-button" @click="userRegist">注册</div>
    </div>
  </div>
</template>

<script>
  import {userLogin, getUsersInfo, userRegist,getShoppingById} from 'network/user'
  export default {
    name: 'LoginAndRegist',
    props: {
      //是否在登录页面
      isLogin: {
        type: Boolean,
        default: true
      }
    },
    data () {
      return {
        userName: '',
        isUserName: false,
        userPsw: '',
        isUserPsw: false,
        regName: '',
        isRegName: false,
        regPsw1: '',
        isRegPsw1: false,
        regPsw2: '',
        isRegPsw2: false,
        regEmail: '',
        isRegEmail: false
      }
    },
    methods: {
      userChange() {
        if(this.userName.length < 3) {
          this.isUserName = true
        } else {
          this.isUserName = false
        }
      },
      pswChange() {
        if(this.userPsw.length < 6) {
          this.isUserPsw = true
        } else {
          this.isUserPsw = false
        }
      },
      regNameChange() {
         if(this.regName.length < 3) {
          this.isRegName = true
        } else {
          this.isRegName = false
        }
      },
      regPsw1Change() {
         if(this.regPsw1.length < 6) {
          this.isRegPsw1 = true
        } else {
          this.isRegPsw1 = false
        }
      },
      regPsw2Change() {
         if(this.regPsw2 != this.regPsw1) {
          this.isRegPsw2 = true
        } else {
          this.isRegPsw2 = false
        }
      },
      regEmailChange() {
        var test=/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
         if(!test.test(this.regEmail)) {
          this.isRegEmail = true
        } else {
          this.isRegEmail = false
        }
      },
      //登录
      userLogin() {
        if(this.userName != '' && this.userPsw != '' && this.isUserName == false && this.isUserPsw == false) {
          var usersVO = {uname:this.userName,upassword:this.userPsw};
          userLogin(this.userName, this.userPsw).then(res => {
            if(res == -1) {
              //用户名不存在
              this.$toast.show("用户名不存在")              
            } else if(res == 0) {
              //密码错误
              this.$toast.show("密码错误")
            } else {
              //获取购物车的信息 保存在store中
              getShoppingById(res).then(res => {
                //保存在store中
                this.$store.dispatch('addCartsById', res).then(res => {
                  
                })
              })
              

              //登录成功  跳转到个人页面  将个人信息存在store中
              getUsersInfo(res).then(res => {
                //将用户的信息保存在store中
                const userInfo = res;
                this.$store.dispatch('addUserInfo', userInfo).then(res => {
                   //跳转到登录注册页面
                  this.$toast.show("登录成功")
                  this.$router.push('/profile')
                })
              })
            }
          })
        } else {
          this.$toast.show("请输入正确信息")
        }   
      },
      //用户注册
      userRegist() {
        if(this.regName != '' && this.regPsw1 != '' && this.regPsw2 != '' && this.regEmail != '' && 
            !this.isRegName && !this.isRegPsw1 && !this.isRegPsw2 && !this.isRegEmail) {
              //发送注册注册请求
              userRegist(this.regName, this.regPsw1, this.regEmail).then(res => {
                if(res == -1) {
                  //用户已存在
                  this.$toast.show("用户名已存在")
                } else if(res == 0) {
                  this.$toast.show("注册失败")
                } else {
                  //注册成功
                  this.$toast.show("注册成功");
                  //跳到登录页面
                  this.$emit('isLoginChange');
                }
              })
        } else {
          this.$toast.show("请输入正确信息")
        }
      }
    }
  }
</script>

<style scoped>
  .login-regist{
    text-align: center;
    position: relative;
  }
  .login-title, .regist-title{
    font-size: 30px;
    margin-top: 30px;
    margin-bottom: 30px;
  }
  .login input, .regist input{
    width: 200px;
    border-radius: 5px;
  }
  .res{
    height: 30px;
    color: red;
  }
  .login-button, .regist-button{
    background-color: forestgreen;
    height: 30px;
    width: 80px;
    margin: auto;
    font-size: 16px;
    line-height: 25px;
    border-radius: 30px;
  }
</style>