<template>
  <div>
    <nav-bar class="nav-barl">
      <div slot="left" class="nav-left" @click="backClick">
      <img src="~assets/img/back.svg" alt="">
    </div>
    <div slot="center" class="nav-titles">{{isNavText}}</div>
    </nav-bar>
    <login-and-regist :isLogin="isLogin" @isLoginChange="isLoginChange"></login-and-regist>
    <div class="changestwo">
      <div v-if="isLogin" @click="goRegist">去注册</div>
      <div v-else @click="goLogin">去登录</div>
    </div>
  </div>
</template>

<script>
  import NavBar from 'common/navbar/NavBar'
  import LoginAndRegist from './childcomps/LoginAndRegist'
  export default {
    name: 'Login',
    components: {
      NavBar,
      LoginAndRegist
    },
    data () {
      return {
        //判断是登录页面还是注册页面
        isLogin: true
      }
    },
    computed: {
      isNavText() {
        if(this.isLogin) {
          return '登录'
        } else {
          return '注册'
        }
      }
    },
    methods: {
      //返回
      backClick() {
        this.$router.back()
      },
      //跳转到注册页面
      goRegist() {
        this.isLogin = false
      },
      //跳转到登录页面
      goLogin() {
        this.isLogin = true
      },
      //从注册跳到登录
      isLoginChange(){
        this.isLogin = true
      }
    }
  }
</script>

<style scoped>
  .nav-barl{
    background-color: #ff8198;
    color: white;
  }
  .nav-left{
    margin-top: 5px;
  }
  .changestwo{
    text-align: center;
    color: #ff8198;
    font-size: 15px;
    margin: auto;
    margin-top: 20px;
  }
</style>