//用户登录
export function userLogin(uname, upassword) { 
  return request({
    url: '/userLogin.do',
    method: 'post',
    data: {
     uname,
     upassword
    }
  });
}

//根据用户id获取用户信息
export function getUsersInfo(uid) {
  return request({
    url: 'userInfo.do',
    method: 'post',
    data: {
      uid
    }
  })
}
//用户注册
export function userRegist(uname, upassword,uemail) {
  return request({
    url: 'userRegist.do',
    method: 'post',
    data: {
      uname,
      upassword,
      uemail
    }
  })
}
//通过id获取购物车商品的信息
export function getShoppingById(uid) {
  return request({
    url: 'getShoppingById.do',
    method: 'post',
    data: {
      uid
    }
  })
}
//修改密码
export function updUpassword(uid,upassword,uemail) {//uemail指用户输入的旧密码
  return request({
    url: 'updUpassword.do',
    method: 'post',
    data: {
      uid,upassword,uemail
    }
  })
}